.Main {
  background-color: #01264f; }
  .Main-content {
    position: relative;
    width: 100vw;
    max-width: 100%;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(80%, #01264f), to(#011226));
    background: -webkit-linear-gradient(top, #01264f 80%, #011226);
    background: -o-linear-gradient(top, #01264f 80%, #011226);
    background: linear-gradient(to bottom, #01264f 80%, #011226);
    z-index: 2;
    margin-top: calc(100vh - 41px); }
    @media screen and (min-width: 768px) {
      .Main-content {
        margin-top: calc(100vh - 70px); } }



